<template>

 <div class="nav">
    <div class="header__logo__text" >
      Logo
    </div>

    <input id="menu-toggle" type="checkbox"/>

    <label class='menu-button-container' for="menu-toggle">

    <div class='menu-button'></div>

    </label>

    <ul class="menu">
       <li
            v-for="(title,index) in titles"
            :key="index"
            :title="title">
            {{title}}
            </li>
    </ul>

  </div>
</template>

<script>
  export default {
    name: "myhumburger",
      data() {
        return {
          //  isMobile: null,
            titles:[
                'Մեր Տեսականին',
                'Համագործակցություն',
                'Կապ',
                'Մեր Մասին',
            ]
        };
      },
  };
</script>

<style scoped>
  .nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content:space-around; 
    color: rgb(16, 15, 15); 
    height: 50px;
    z-index: 999;
    position: relative;
    width: 100%;
  }

  .menu {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    margin-top: 10px;
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .menu > li {
    /* margin: 0 1rem; */
    overflow: hidden;
  }

  .menu-button-container {
    display: none;
    height: 100%;
    width: 30px;
    cursor: pointer;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  #menu-toggle {
    display: none;
  }

  .menu-button,
  .menu-button::before,
  .menu-button::after {
    display: block;
    background-color: rgb(20, 18, 18);
    position: absolute;
    height: 4px;
    width: 40px;
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    border-radius: 2px;
  }

  .menu-button::before {
    content: '';
    margin-top: -12px;
  }

  .menu-button::after {
    content: '';
    margin-top: 12px;
  }

  #menu-toggle:checked + .menu-button-container .menu-button::before {
    margin-top: 0px;
    transform: rotate(405deg);
  }

  #menu-toggle:checked + .menu-button-container .menu-button {
    background: rgba(255, 255, 255, 0);
  }

  #menu-toggle:checked + .menu-button-container .menu-button::after {
    margin-top: 0px;
    transform: rotate(-405deg);
  }

  @media (min-width: 900px) {
    .nav{
      display: none;
    }

  }

  @media (max-width: 900px) {
    .menu-button-container {
      display: flex;
    }
    .menu {
      position: absolute;
      top: 0;
      margin-top: 60px;
      left: 0;
      flex-direction: column;
      /* width: 100%; */
      justify-content: center;
      align-items: center;
    }
    #menu-toggle ~ .menu li {
      width: 100%;
      height: 0;
      margin: 0;
      padding: 0;
      border: 0;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    #menu-toggle:checked ~ .menu li {
      border: 1px solid #535353;
      height: 2.5em;
      padding: 0.5em;
      transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
    }
    .menu > li {
      display: flex;
      justify-content: center;
      margin: 0;
      /* padding: 0.5em 0; */
      width: 100%;
      color: white;
      background-color: #222;
    }
    .menu > li:not(:last-child) {
      border-bottom: 1px solid #444;
    }
  }
